<template>
    <div>
        <div class="kd-page-title">打印信确认</div>
        <div class="kd-page-content-wrapper wrap">
            <p class="title">基本信息</p>
            <div class="baseWrap">
                <div class="baseItem">
                    <p>姓名：</p>
                    <span v-for="(item, index) in pyCompleteList" :key="index"> {{item.text}} <span :class="item.isMultitone ? 'red' : ''">({{item.py}})</span> </span>
                </div>
                <div class="baseItem">
                    <p>身份证号：</p>
                    <span>{{identifyCard.certNumber}}</span>
                    <p>出生日期：</p>
                    <span>{{identifyCard.bornDay}}</span>
                </div>
                <div class="baseItem">
                    <p>性别：</p>
                    <span>{{identifyCard.gender === 1 ? '男' : '女'}}</span>
                    <p>民族：</p>
                    <span>{{identifyCard.nation}}</span>
                </div>
            </div>
            <p class="title" style="margin-top: 40px">申请信息</p>
            <div class="applyItem" v-if="qrInfo && qrInfo.hz">
                <p>{{qrInfo.hz.bzlx}}</p>
                <p>{{qrInfo.hz.qwd}}</p>
                <p>{{qrInfo.hz.cjsy}}</p>
            </div>
            <div class="applyItem" v-if="qrInfo && qrInfo.ga">
                <p>{{qrInfo.ga.bzlx}}</p>
                <p>{{qrInfo.ga.xgqzzl}}</p>
                <p>{{qrInfo.ga.xgqzyxcs}}</p>
                <p>{{qrInfo.ga.amqzzl}}</p>
                <p>{{qrInfo.ga.amqzyxcs}}</p>
            </div>
            <div class="applyItem" v-if="qrInfo && qrInfo.tw">
                <p>{{qrInfo.tw.bzlx}}</p>
                <p>{{qrInfo.tw.qzzl}}</p>
                <p>{{qrInfo.tw.qzyxcs}}</p>
            </div>
        </div>
        <div class="kd-foot-button kd-foot-button-position" v-if="!$store.state.common.visibleTip">
            <kd-button icon="kedaIcon-policework" text="返回首页" type-class="home"/>

            <kd-button icon="kedaIcon-chahao" text="申请信息有误" :button-handle="errorHandle"/>
            <kd-button icon="kedaIcon-print" text="打印" :button-handle="goDown"/>
        </div>
    </div>
</template>

<script>

import { mapState } from 'vuex';

export default {
    name: 'exitEntryPrintSure',
    props: {
        qrInfo: { type: Object, default: () => {} },
        pyCompleteList: { type: Array, default: () => [] },
        nextFunction: {
            type: Function,
            default: function () {
                this.changeStep('down');
            }
        },
        errorFun: {
            type: Function,
            default: function () {
                this.changeStep('back');
            }
        }
    },
    computed: {
        ...mapState('common', {
            identifyCard: 'identifyCard'
        })
    },
    data () {
        return {};
    },
    mounted () {

    },
    methods: {
        async errorHandle () {
            await this.errorFun();
        },
        async goDown () {
            await this.nextFunction();
        }
    }
};
</script>

<style scoped lang="scss">
    .title {
        font-size: 30px;
        margin-bottom: 20PX;
    }

    .applyItem {
        display: flex;

        p {
            margin-right: 20px;
            font-size: 26px;
        }

        p:before {
            content: "●";
        }
    }

    .baseItem {
        display: flex;
        margin-bottom: 10PX;
        > p {
            font-size:26px;
            margin-right: 10px;
        }
        > span {
            font-size:26px;
            font-weight:600;
            color:rgba(255,255,255,1);
            display: block;
            margin-right: 10px;
        }
    }

    .red {
        color: #FE5701;
    }
</style>
