<template>
    <div>
        <div class="kd-page-title">多音字（点击选择）</div>
        <div class="kd-page-content-wrapper wrap">
            <div class="nameWrap" v-if="!chooseComplete">
                <div
                    class="nameItem"
                    v-for="(item, index) in list"
                    :key="index"
                    :class="currentName === index ? 'chooseName' : ''"
                    @click="chooseName(index)"
                >{{item.hannzi}}</div>
            </div>
            <div class="nameWrap" v-if="!chooseComplete">
                <div
                    v-for="(item, index) in list[currentName].pyList"
                    :key="index"
                    class="pingyinItem"
                    :class="currentPingyin && currentPingyin[currentName] && currentPingyin[currentName].py === item ? 'pingyinItemChoose' : ''"
                    @click="choosePingyin(list[currentName].hannzi, item)"
                >{{item}}
                </div>
            </div>
            <div class="nameWrap" v-if="chooseComplete">
                <div
                    v-for="(item, index) in currentPingyin"
                    :key="index"
                    class="pingyinCompleteItem"
                    :class="item.isMultitone ? 'chooseName' : ''"
                >
                    <p>{{item.text}}</p>
                    <p>{{item.py}}</p>
                </div>
            </div>
        </div>
        <div class="kd-foot-button kd-foot-button-position" v-if="!$store.state.common.visibleTip">
            <kd-button icon="kedaIcon-policework" text="返回首页" type-class="home"/>
            <kd-button v-if="chooseComplete" icon="kedaIcon-shuru" text="修改" :button-handle="edit"/>
            <kd-button v-if="chooseComplete" icon="kedaIcon-weixuanz" text="确定" :button-handle="goDown"/>
        </div>
    </div>
</template>

<script>

export default {
    name: 'pyList',
    props: {
        zwx: { type: String, default: '' },
        zwm: { type: String, default: '' },
        list: { type: Array, default: () => [] },
        nextFunction: { type: Function, default: function () { this.changeStep('down'); } }
    },
    data () {
        return {
            currentName: 0,
            currentPingyin: [],
            chooseComplete: false
        };
    },
    mounted () {
        this.list.forEach((item) => {
            this.$set(this.currentPingyin, this.currentPingyin.length, { text: '', py: '', isMultitone: false });
        });
    },
    methods: {
        goDown () {
            const self = this;
            const firstArr = self.zwx.split('');
            let first = '';
            firstArr.forEach((i) => {
                const match = self.currentPingyin.find((j) => {
                    return j.text === i;
                });
                if (match) {
                    first = first + match.py;
                }
            });

            const secondArr = self.zwm.split('');
            let second = '';
            secondArr.forEach((i) => {
                const match = self.currentPingyin.find((j) => {
                    return j.text === i;
                });
                if (match) {
                    second = second + match.py;
                }
            });
            this.nextFunction(first, second, this.currentPingyin);
        },
        edit () {
            this.currentPingyin = [];
            this.chooseComplete = false;
            this.currentName = 0;
            this.list.forEach((item) => {
                this.$set(this.currentPingyin, this.currentPingyin.length, { text: '', py: '', isMultitone: false });
            });
        },
        chooseName (index) {
            this.currentName = index;
        },
        choosePingyin (text, py) {
            this.$set(this.currentPingyin, this.currentName, { text, py, isMultitone: this.list[this.currentName].pyList.length > 1 });
            if (this.currentName + 1 < this.currentPingyin.length && this.currentName >= 0) {
                this.currentName = this.currentName + 1;
            } else {
                this.chooseComplete = true;
            }
            console.log(this.currentPingyin);
        }
    }
};
</script>

<style scoped lang="scss">
    .wrap {
        display: flex;
        flex-direction: column;
        justify-content: space-evenly;
    }
    .nameWrap {
        display: flex;
        justify-content: space-evenly;
    }
    .nameItem {
        width: 116px;
        height: 116px;
        background: linear-gradient(180deg, rgba(112, 163, 216, 1) 0%, rgba(58, 132, 191, 1) 100%);
        border-radius: 6px;
        border: 1px solid #ffffff;
        color: #ffffff;
        display: flex;
        justify-content: center;
        align-items: center;
        font-size: 56px;
    }

    .pingyinWrap {
        display: flex;
        justify-content: space-evenly;
    }

    .pingyinItem {
        width: 280px;
        height: 102px;
        background: rgba(255, 255, 255, 0.1);
        border-radius: 6px;
        border: 1px dashed rgba(242, 249, 255, 1);
        display: flex;
        justify-content: center;
        align-items: center;
        font-size: 56px;
    }

    .pingyinItemChoose {
        width: 280px;
        height: 102px;
        background: rgba(255, 255, 255, 0.2);
        border-radius: 6px;
        border: 1px solid rgba(242, 249, 255, 1);
    }

    .chooseName {
        color: #FE5701;
    }

    .pingyinCompleteItem {
        width:240px;
        height:240px;
        background:rgba(255,255,255,0.1);
        border-radius:6px;
        border:1px dashed rgba(242,249,255,1);
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        font-size: 56px;
    }

</style>
