<template>
    <div>
        <identify-card-page v-if="currentStep === steps.findIndex(step=>step.key === 'identifyCardPage')"/>
        <face-yitu v-if="currentStep === steps.findIndex(step=>step.key === 'faceYitu')"/>
        <contrast v-if="currentStep === steps.findIndex(step=>step.key === 'contrast')"/>

        <process :type="1" v-if="currentStep === steps.findIndex(step=>step.key === 'checkProcess')" :handle-business="checkProcess"/>
        <photo-tip-second
            v-if="currentStep === steps.findIndex(step=>step.key === 'photoTip')"
            :nextFunction="photoNext"
            :photoNumber="3"
        />

        <excel-qr v-if="currentStep === steps.findIndex(step=>step.key === 'excelQR')" :next-function="qrComplete"></excel-qr>
        <py-list
            v-if="currentStep === steps.findIndex(step=>step.key === 'pyList')"
            :list="pyList"
            :next-function="excelComplete"
            :zwx="zwx"
            :zwm="zwm"
        ></py-list>

        <exit-entry-print-sure
            v-if="currentStep === steps.findIndex(step=>step.key === 'infoSure')"
            :qrInfo="qrInfo"
            :pyCompleteList="pyCompleteList"
            :next-function="printSubmit"
            :error-fun="backApply"
        ></exit-entry-print-sure>
    </div>
</template>

<script>
import { mapState, mapMutations } from 'vuex';
import identifyCardPage from '@/view/public/before-work/identify-card-page';
import faceYitu from '@/view/public/before-work/face-yitu';
import contrast from '@/view/public/before-work/contrast';
import process from '@/components/process';
import photoTipSecond from '@/view/pageComponents/photo-tip-second';
import excelQr from './../components/excelQr';
import pyList from './components/pyList';
import exitEntryPrintSure from './components/exitEntryPrintSure';
import { EXIT_ENTRY_PRINT } from '@/constant/stepsOption';
import { handleExitEntryPhotoRequest } from '@/utils/axiosHandle';
import { cancelPrefix } from '@/utils/tools';
import { exitEntryTakePhoto, exitEntryPhotoFinish, exitEntryCheckApplyStatus, exitEntrySubmitPrint } from '@/service/exitEntry/entryAndExit';
import { checkIdInfo, duplexPrint, savePhotoClient } from '@/service/common';

export default {
    name: 'exitEntryPrint',
    components: { identifyCardPage, faceYitu, contrast, process, photoTipSecond, excelQr, pyList, exitEntryPrintSure },
    computed: {
        ...mapState('common', {
            steps: 'steps',
            currentStep: 'currentStep',
            businessTypeCode: 'businessTypeCode',
            binocularCamera: 'binocularCamera',
            identifyCard: 'identifyCard'
        })
    },
    data () {
        return {
            photoBase: '',
            zwx: '丁',
            zwm: '万朋',
            zp: '',
            zwxpy: '',
            zwmpy: '',
            dyz: 0,
            qrInfo: {},
            pyList: [],
            pyCompleteList: []
        };
    },
    methods: {
        ...mapMutations('common', ['setSteps', 'changeStep', 'changeTipPage', 'setDogHandlePic', 'changeVisible']),
        async photoNext (base64) {
            this.photoBase = cancelPrefix(base64);
            await this.contrast();
        },
        async contrast () {
            const res = await checkIdInfo({
                photo: this.photoBase,
                identityPic: cancelPrefix(this.identifyCard.identityPic)
            }, true);
            if (res && res.result && res.result.respData && res.result.respData.code === '0') {
                await this.dogIdHandle();
            } else if (res) {
                this.changeTipPage({ message: '检测结果与身份证信息不匹配，请您重新检测…', imgName: 'imgTexts' });
            }
        },
        async dogIdHandle () { // 抠图处理
            const res = await handleExitEntryPhotoRequest(this.photoBase, this.CONSTANT);
            if (res && res.code === 1) {
                this.photoBase = res.obj;
                await this.saveLocal();
            } else {
                this.changeTipPage({ message: res.message, enableBack: true });
            }
        },
        async saveLocal () { // 照片存储本地
            const res = await savePhotoClient({
                filePath: this.businessTypeCode,
                fileName: this.identifyCard.certNumber,
                base64: cancelPrefix(this.photoBase)
            });
            if (res && res.code === 1) {
                await this.uploadPhoto();
            } else {
                this.changeTipPage({ message: res.message, enableBack: true });
            }
        },
        async uploadPhoto () { // 存储数据库
            const res = await exitEntryTakePhoto({ // 照片存储数据库
                identifiedPhoto: cancelPrefix(this.photoBase),
                idNum: this.identifyCard.certNumber
            });
            if (res && res.code === '0') {
                this.changeStep('checkProcess');
            } else if (res) {
                this.changeTipPage({ message: res.message, enableBack: true });
            }
        },
        async checkProcess () {
            const res = await exitEntryCheckApplyStatus({ // 照片存储数据库
                idNum: this.identifyCard.certNumber,
                name: this.identifyCard.partyName
            });
            if (res && res.code === '0') {
                this.zp = res.result.base64;
                this.qrInfo = res.result.applyInfo;
                this.zwx = res.result.zwx;
                this.zwm = res.result.zwm;
                this.pyList = res.result.pyDTOList;

                this.changeStep('pyList');
            } else if (res) {
                if (res.code === '-3') {
                    this.changeTipPage({
                        message: '您还未采集证件照片，请先进行照片采集',
                        extraBut: true,
                        extraIcon: 'kedaIcon-bhjmianbushibie',
                        extraText: '照片采集',
                        extraFun: () => {
                            this.changeStep('photoTip');
                            this.changeVisible({ visibleTip: false, visibleSteps: true, visibleBusinessName: true });
                        }
                    });
                } else if (res.code === '-2') {
                    this.changeTipPage({
                        message: '您还未填写申请表信息，请先填写申请表',
                        extraBut: true,
                        extraIcon: 'kedaIcon-tubiaolunkuo-',
                        extraText: '扫码填表',
                        extraFun: () => {
                            this.changeStep('excelQR');
                            this.changeVisible({ visibleTip: false, visibleSteps: true, visibleBusinessName: true });
                        }
                    });
                } else {
                    this.changeTipPage({ message: res.message, enableBack: true });
                }
            }
        },
        qrComplete () {
            this.changeStep('checkProcess');
        },
        async excelComplete (first, second, list) {
            this.zwxpy = first;
            this.zwmpy = second;
            this.pyList.forEach((i) => {
                if (i && i.pyList) {
                    i.pyList.forEach((j) => {
                        if (j.length > 0) {
                            this.dyz = 1;
                        }
                    });
                }
            });
            this.pyCompleteList = list;
            this.changeStep('infoSure');
        },
        async completeBusiness () {
            const res = await exitEntryPhotoFinish({});
            if (res && res.code === '0') {
                this.result = res.result;
                this.changeStep('down');
            } else if (res) {
                this.changeTipPage({ message: res.message });
            }
        },
        backApply () {
            this.changeStep('excelQR');
        },
        async printSubmit () {
            const timeStart = this.$moment(this.identifyCard.effDate).format('YYYY.MM.DD');
            const endStart = this.$moment(this.identifyCard.expDate).format('YYYY.MM.DD');
            const payload = {
                qfjg: this.identifyCard.certOrg,
                xm: this.identifyCard.partyName, // 姓名
                sex: this.identifyCard.gender === 1 ? '男' : '女', // 性别 eg： 男
                nation: this.identifyCard.nation, // 民族 eg： 汉
                birthDay: this.$moment(this.identifyCard.bornDay).format('YYYY-MM-DD'), // 生日 2020-07-29
                address: this.identifyCard.certAddress, // 地址
                idNum: this.identifyCard.certNumber, // 身份证号码
                effectiveDate: timeStart + '-' + endStart, // 有效期起始日期 eg: 2020.07.29-2020.07.29
                pyx: this.zwxpy, // 拼音姓
                pym: this.zwmpy, // 拼音名
                dyz: this.dyz,
                zp: cancelPrefix(this.identifyCard.identityPic) // 身份证照片base64
            };

            const res = await exitEntrySubmitPrint(payload);
            if (res && res.code === '0') {
                await this.printInfo(res.result.pdfBase64);
            } else {
                this.changeTipPage({ message: res.message, enableBack: true });
            }
        },
        async printInfo (pdfBase64) {
            const res = await duplexPrint({
                pdfBase64: pdfBase64
            });
            if (res && res.code === 1) {
                this.changeTipPage({ message: '打印完成，请及时取回您的申请表', imgName: 'imgPrint' });
            } else {
                this.changeTipPage({ message: res.message, enableBack: true });
            }
        }
    },
    mounted () {
        this.setSteps(EXIT_ENTRY_PRINT);
    }
};
</script>
<style lang="scss" scoped>

</style>
